.sucsess-page, .thankyou-popup {
  background-color: #fff;
  height: calc(100vh - 116.1px);
}

.sucsess-page_content {
  height: calc(100vh - 116.1px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 570px;
  margin: 0 auto;
}

.sucsess-page_content img {
  display: block;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  margin-bottom: 45px;
}

.sucsess-page_content h1 {
  font-size: 36px;
  line-height: 50px;
  text-align: center;
  margin-bottom: 15px;
}

.sucsess-page_content p {
  font-size: 19px;
  line-height: 28px;
  text-align: center;
}

.sucsess-page_content p a {
  color: #23A8CD;
}

.thankyou-popup {
  display: none;
  position: fixed;
  width: 100%;
  background: #fff;
  z-index: 999;
  top: 0;
  height: 100%;
}

.thankyou-popup .popup-inner {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.thankyou-popup .popup-content {
  max-width: 570px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.thankyou-popup .popup-content img {
  display: block;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  margin-bottom: 45px;
}

.thankyou-popup .popup-content h4 {
  font-size: 36px;
  line-height: 50px;
  text-align: center;
  margin-bottom: 15px;
}

.thankyou-popup .popup-content p {
  font-size: 19px;
  line-height: 28px;
  text-align: center;
}

.thankyou-popup .popup-content p a {
  color: #23A8CD;
}

@media screen and (max-width: 990px) {
  .sucsess-page, .thankyou-popup {
    height: calc(100vh - 75.11px);
  }
  .thankyou-popup {
    top: 0;
    height: 100%;
  }
  .sucsess-page_content {
    height: calc(100vh - 75.11px);
  }
}

@media screen and (max-width: 500px) {
  .sucsess-page, .thankyou-popup {
    height: calc(100vh - 83.65px);
  }
  .thankyou-popup {
    top: 0;
    height: 100%;
  }
  .sucsess-page_content {
    height: calc(100vh - 83.65px);
  }
  .sucsess-page_content p {
    font-size: 17px;
  }
  .thankyou-popup .popup-content p {
    font-size: 17px;
  }
}

